import { Box, Card, CardContent, Paper, Container, Typography, CardActions, Button, List, ListItem, Divider } from "@mui/material";
import { useSelector } from "react-redux";
import CheckIcon from '@mui/icons-material/Check';
import { AccountCircle } from '@mui/icons-material';

const RegisterMembers = ({}) => {
    const registeredMembers = useSelector((state) => state.attendance.allMembers);
    return (
        <Container component="main" maxWidth="sm">
        <Card sx={{ my: 4, }}>
        <CardContent>

        <Typography gutterBottom variant="h5" component="div" sx={{my: 2}} align="center">Register signed members</Typography>
        <Box
        sx={{
            mb: 2,
            display: "flex",
            flexDirection: "column",
            height: 400,
            overflow: "hidden",
            overflowY: "scroll",
            // justifyContent="flex-end" # DO NOT USE THIS WITH 'scroll'
            }}
        >            
        <List aria-label="members">
        {registeredMembers.map((member) => (
            
            [<ListItem button>
                <AccountCircle/>
            <Typography variant="h7" sx={{my: 1, mx:1}}>
             {` ${member.username} signed `} 
            </Typography>
            <CheckIcon style={{fill: "green"}}/>
            </ListItem>,
            <Divider />]
            
            
        ))}
        </List>
        
        </Box>
        </CardContent>
        <CardActions>
            <Button size="small" color="primary" sx={{width: "100%"}}>
            Register {registeredMembers.length} members
            </Button>
        </CardActions>
        </Card>
        </Container>
    )
}

export default RegisterMembers;