import { Card, CardContent, Container, Typography, Box } from "@mui/material";
import logo from '../../images/logo.jpg';
import { useSelector } from "react-redux";

const Signature = ({}) => {
    const username = useSelector((state) => state.auth.username);
    const signature = useSelector((state) => state.auth.signature);
    return (
        <Container component="main" maxWidth="sm">
        <Card sx={{ my: 4, }}>
        <CardContent>

        <Typography gutterBottom variant="h6" component="div" sx={{my: 1}} align="center">SIGNATURE</Typography>
        <Typography gutterBottom variant="h4" component="div" sx={{my: 4}} align="center">{signature}</Typography>
        <Typography gutterBottom variant="h7" component="div" sx={{my: 1}} align="center">User: {username}</Typography>
        <Box sx={{my:5}}>
        <img src={logo} style={{width : "100%",}} title="Al-Ibad School Rawalpindi, Pakistan" alt="Al-Ibad School Rawalpindi, Pakistan"></img>
        </Box>
        </CardContent>
        </Card>
        </Container>
    )
}

export default Signature;