import React from "react";
import { useDispatch, useSelector } from "react-redux";
import Grid from '@mui/material/Unstable_Grid2';
import { taskActions, STATUSES } from '../../store/tasks/slice';
import Task from './Task';
import { Typography, Paper, IconButton, InputBase, Card, CardContent, Tabs, Tab, Container, Box } from "@mui/material";
import AddIcon from '@mui/icons-material/Add';

const Tasks = () => {
    const allTasks = useSelector((state) => state.tasks.allTasks);
    const activeCategory = useSelector((state) => state.tasks.activeCategory);
    const activeCategoryTasks = allTasks.filter((task) => task.status===STATUSES[activeCategory]);
    const dispatch = useDispatch();
    const handleChange = (event, newValue) => {
        dispatch(taskActions.setActiveCategory(newValue));
    }
    return(
        <Container component="main" maxWidth="sm">
        <Card sx={{ minWidth: 275, my: 4, }}>
        <CardContent>
        <Grid container spacing={1}>
        <Grid xs={12}>
        <Typography gutterBottom variant="h6" component="div" align="center">All Tasks</Typography>
        </Grid>
        <Grid xs={12}>
        <Tabs value={activeCategory} onChange={handleChange} aria-label="Tasks tabs" variant="scrollable" centered>
            <Tab label="Backlog" />
            <Tab label="Todo" />
            <Tab label="In Progress" />
            <Tab label="Completed" />
        </Tabs>
        </Grid>
        <Grid xs={12}>
        <Paper
        component="form"
        sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', width: "100%" }}
        >
        <InputBase
            sx={{ ml: 1, flex: 1 }}
            placeholder="Type a new task"
            inputProps={{ 'aria-label': 'type a new task' }}
        />
        <IconButton type="button" sx={{ p: '10px' }} aria-label="add">
            <AddIcon />
        </IconButton>
        </Paper>
        </Grid>
        <Grid xs={12} className="panel-content" >
        <Box
        sx={{
            mb: 2,
            display: "flex",
            flexDirection: "column",
            height: 400,
            overflow: "hidden",
            overflowY: "scroll",
            // justifyContent="flex-end" # DO NOT USE THIS WITH 'scroll'
            }}
        >
        <Grid container spacing={2}>
            {activeCategoryTasks.length !== 0 ? 
                (activeCategoryTasks.map((task) => (
                    <Task task={task}/>
                ))) :
                (
                    <Grid xs={12}>
                    <Typography gutterBottom variant="h5" component="div" sx={{my: 5}} align="center">Welcome to the void!</Typography>
                    </Grid>
                )
            }
        </Grid>
        </Box>
        
        </Grid>
        </Grid>
        </CardContent>
        </Card>
        </Container>
    )
}

export default Tasks;