import { createSlice } from "@reduxjs/toolkit";

export const STATUSES = ["backlog", "todo", "inprogress", "completed"];

const taskSlice = createSlice({
  name: "tasks",
  initialState: { 
    allTasks: [
        {id: 1, title: "Kuch krna he", description: "this is a long and lengthy description", date: "21-9-22", time: "22:00", status: "backlog"},
        {id: 2, title: "Kuch krna he2", description: "this is a long and lengthy description", date: "21-9-22", time: "22:00", status: "backlog"},
        {id: 4, title: "Kuch krna he2", description: "this is a long and lengthy description", date: "21-9-22", time: "22:00", status: "completed"}
    ],
    activeCategory: 0,
    },
  reducers: {
    toggle(state, action) {
      state[action.payload] = !state[action.payload];
    },
    setActiveCategory(state, action) {
      state.activeCategory = action.payload;
    },
    updateStatus(state, action){
      let i = state.allTasks.findIndex((task) => task.id === action.payload.task.id);
      state.allTasks[i].status = STATUSES[STATUSES.indexOf(action.payload.task.status) + action.payload.updateBy];
    }
  },
});

export const taskActions = taskSlice.actions;

export default taskSlice;