import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import { useDispatch, useSelector } from 'react-redux'; 
import { uiActions } from '../store/ui/slice';
import { Link } from 'react-router-dom';
import { AccountBox, Person, Logout, Login, HowToReg } from '@mui/icons-material';
import { authActions } from '../store/auth/slice';

const ResponsiveAppBar = () => {
    const navbarToggle = useSelector((state) => state.ui.navbarToggle);
    const userMenuToggle = useSelector((state) => state.ui.userMenuToggle);
    const isLoggedIn = useSelector((state) => state.auth.isLoggedIn);
    const username = useSelector((state) => state.auth.username);
    const dispatch = useDispatch();
    const toggle = (menu) => dispatch(uiActions.toggle(menu));

  return (
    <AppBar position="static">
      <Container maxWidth="xl" className='theme-color'>
        <Toolbar disableGutters>
          <Typography
            variant="h6"
            noWrap
            component="a"
            href="/"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontFamily: 'Hoefler Text',
              fontWeight: 700,
              color: 'white',
              textDecoration: 'none',
            }}
            className="logo"
          >
            Al-Ibad School
          </Typography>

          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={() => toggle("navbarToggle")}
              color="inherit"
            >
              <MenuIcon  style={{ fill: 'white' }}/>
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={navbarToggle}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={navbarToggle}
              onClose={() => toggle("navbarToggle")}
              sx={{
                display: { xs: 'block', md: 'none' },
                mt: '45px',
              }}
            >
            
            <Link to="home" className='navbar-link'>
              <MenuItem key="home" onClick={() => toggle("navbarToggle")}>  
                <Typography textAlign="center">Home</Typography>
              </MenuItem>
            </Link>
            

            { isLoggedIn &&
              [
              <Link to="signature" className='navbar-link'>
              <MenuItem key="signature" onClick={() => toggle("navbarToggle")}>
                  <Typography textAlign="center">Signature</Typography>
                  </MenuItem>
              </Link>
              ,
              
              <Link to="tasks" className='navbar-link'>
                <MenuItem key="tasks" onClick={() => toggle("navbarToggle")}>
                  <Typography textAlign="center">Tasks</Typography>
                  </MenuItem>
              </Link>
              ,
              <Link to="attendance" className='navbar-link'>
              <MenuItem key="attendance" onClick={() => toggle("navbarToggle")}>
                <Typography textAlign="center">Attendance</Typography>
              </MenuItem>
              </Link>
              ]
            }
            <Link to="support" className='navbar-link'>
            <MenuItem key="support" onClick={() => toggle("navbarToggle")}>
              <Typography textAlign="center">Support</Typography>
            </MenuItem>
            </Link>
            </Menu>
          </Box>
          <Typography
            variant="h5"
            noWrap
            component="a"
            href=""
            sx={{
              mr: 2,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontFamily: 'Hoefler Text',
              fontWeight: 700,
              color: 'white',
              textDecoration: 'none',
            }}
            className="logo"
          >
            Al-Ibad School
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
            <Link to="home" className='navbar-link'>
                <Typography textAlign="center" sx={{color: 'white', mx: 1}}>Home</Typography>
            </Link>
            { isLoggedIn &&
            [
              <Link to="signature" className='navbar-link'>
                <Typography textAlign="center" sx={{color: 'white', mx: 1}}>Signature</Typography>
              </Link>,
              <Link to="tasks" className='navbar-link'>
                <Typography textAlign="center" sx={{color: 'white', mx: 1}}>Tasks</Typography>
              </Link>,
              <Link to="attendance" className='navbar-link'>
                  <Typography textAlign="center" sx={{color: 'white', mx: 1}}>Attendance</Typography>
              </Link>
            ]
            }
            <Link to="support" className='navbar-link'>
                <Typography textAlign="center" sx={{color: 'white', mx: 1}}>Support</Typography>
            </Link>
          </Box>

          <Box sx={{ flexGrow: 0 }}>
            
            {isLoggedIn ?
            (

            <Tooltip title="Logout">
              <IconButton onClick={() => dispatch(authActions.logout())} sx={{ p: 0}}>
              <Logout style={{ fill: 'white' }}/>
              <Typography textAlign="center" sx={{color:"white"}}>Logout</Typography>
              </IconButton>
            </Tooltip>
            
            )
            :
            (
            <Tooltip title="Login">
              <IconButton onClick={() => toggle("userMenuToggle")} sx={{ p: 0 }}>
              <Login style={{ fill: 'white' }}/>
              <Typography textAlign="center" sx={{color:"white"}}> Login</Typography>
              </IconButton>
            </Tooltip>
            )
            }
            
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={userMenuToggle}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={userMenuToggle}
              onClose={() => toggle("userMenuToggle")}
            >
            <Link to="studentlogin" className='navbar-link'>
              <MenuItem key="studentLogin" onClick={() => toggle("userMenuToggle")}>
              <AccountBox/> 
              <Typography textAlign="center">Student</Typography>
                
              </MenuItem>
            </Link>
            <Link to="stafflogin" className='navbar-link'>
            <MenuItem key="staffLogin" onClick={() => toggle("userMenuToggle")}>
              <Person/> 
              <Typography textAlign="center">Teacher</Typography>
              
            </MenuItem>
            </Link>
            <Link to="register" className='navbar-link'>
            <MenuItem key="register" onClick={() => toggle("userMenuToggle")}>
              <HowToReg/>
              <Typography textAlign="center">Register</Typography>
              
            </MenuItem>
            </Link>
            
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};
export default ResponsiveAppBar;
