import { useDispatch } from "react-redux";
import { taskActions } from "../../store/tasks/slice";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import Grid from '@mui/material/Unstable_Grid2';
import { Card, CardContent, CardActionArea, CardActions, Typography, Button, Divider } from "@mui/material";

const Task = ({task}) => {
    const { id, title, description, date, time, status } = task;
    const dispatch = useDispatch();
    const updateStatus = (updateBy) => dispatch(taskActions.updateStatus({task: task, updateBy: updateBy}));
    return (
        <Grid xs={12}>
        <Card sx={{ width: "100%"}}>
        <CardActionArea>
            <CardContent>
            <Typography gutterBottom variant="h5" component="div">
                {title}
            </Typography>
            <Typography variant="body2" color="text.secondary">
                {description}
                <Divider sx={{my:2}}/>
                {time} {date}
            </Typography>
            </CardContent>
        </CardActionArea>
        <CardActions>
            
            {status !== "backlog" && 
            <Button size="small" color="primary" sx={{width: "100%"}} onClick={() => updateStatus(-1)}>
            <ArrowBackIcon  style={{ fill: 'green' }}/>
            </Button>
            }
            
            {status !== "completed" && 
            <Button size="small" color="primary" sx={{width: "100%"}} onClick={() => updateStatus(1)}>
            <ArrowForwardIcon  style={{ fill: 'green' }}/>
            </Button>
            }
        </CardActions>
        </Card>
        </Grid>
    )
}

export default Task;