import axios from "axios";
import { authActions } from "./slice";
import { uiActions } from "../ui/slice";

export const login = (credentials) => {
    return async (dispatch) => {
      const loginHandler = async () => {
        var config = {
          method: 'post',
          url: '/token-auth/',
          headers: { 
            'Content-Type': 'application/json'
          },
          data : credentials
        };
        var data = null;
        await axios(config)
        .then((res) => data = JSON.stringify(res.data))
        .catch((err) => console.log(err));
        
        return data;
      };
  
      try {
        const response = await loginHandler();
        window.localStorage.setItem("token", JSON.parse(response).token);
        window.localStorage.setItem("username", JSON.parse(credentials).username);

        dispatch(authActions.login(JSON.parse(credentials).username));
      } 
      catch (err) {
          dispatch(
              uiActions.showNotification({
              open: true,
              message: "Login Failed",
              type: "danger",
              })
          );
      }
    };
  };

