import logo from '../images/logo.jpg';
import { Box, Container, Typography } from '@mui/material';

export default function(){
    return (
        <Container component="main" maxWidth="sm">
        <Box sx={{my:4}}>
            <Typography gutterBottom variant="h5" component="div" sx={{my: 1}} align="center">
            Please contact the office for any sign ups.
            </Typography>
            <Box sx={{my:5}}>
            <img src={logo} style={{width : "100%",}} title="Al-Ibad School Rawalpindi, Pakistan" alt="Al-Ibad School Rawalpindi, Pakistan"></img>
            </Box>
        </Box>
        </Container>
    )
}