import { login } from '../store/auth/actions';
import { useDispatch } from 'react-redux';
import { Container, Grid, Checkbox, Link, Typography, Box, FormControlLabel, CssBaseline, InputAdornment, TextField, Button} from '@mui/material';
import { AccountCircle, Message, Person } from '@mui/icons-material';

const required = (val) => val && val.length;
// const maxLength = (len) => (val) => !(val) || (val.length <= len);
// const minLength = (len) => (val) => val && (val.length >= len);
// const isNumber = (val) => !isNaN(Number(val));
// const validEmail = (val) => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);

export default function(){
    const dispatch = useDispatch();
    const handleSubmit = (values) => {
        const creds = JSON.stringify(values);
        alert(creds);
    }

    return(
      <Container component="main" maxWidth="sm">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 5,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Typography component="h1" variant="h5">
            Send us a message
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="name"
              label="Name"
              name="name"
              autoComplete="name"
              InputProps={{
                    startAdornment: (
                        <InputAdornment position="start">
                        <Person />
                        </InputAdornment>
                    ),
                    }}
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email"
              name="email"
              autoComplete="email"
              InputProps={{
                    startAdornment: (
                        <InputAdornment position="start">
                        <AccountCircle />
                        </InputAdornment>
                    ),
                    }}
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              id="message"
              label="Message"
              name="message"
              autoComplete="message"
              multiline
              rows={5}
              InputProps={{
                    startAdornment: (
                        <InputAdornment position="start">
                        <Message />
                        </InputAdornment>
                    ),
                    }}
              autoFocus
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
                Send
            </Button>
          </Box>
        </Box>
      </Container>
    )
}
