import {Link, Route, Routes} from 'react-router-dom';
import Home from './Home';
import StudentLogin from './auth/StudentLogin';
import StaffLogin from './auth/StaffLogin';
import Support from './Support';
import Notification from './Notification';
import Footer from './Footer';
import Navbar from './Navbar';
import Tasks from './tasks/Tasks';
import { useSelector, useDispatch } from 'react-redux';
import React from 'react';
import MembersList from './attendance/MembersList';
import RegisterMembers from './attendance/RegisterMembers';
import Signature from './attendance/Signature';

export default function(){
    const notification = useSelector((state) => state.ui.notification);
    return(
    <React.Fragment>
      <Navbar/>
      <div className='content-wrap'>
      {notification && (
        <Notification type={notification.type} message={notification.message} />
      )}

      <Routes>
            <Route path='home' element={<Home/>}/>
            <Route path='tasks' element={<Tasks/>}/>
            <Route path='studentlogin' element={<StudentLogin/>}/>
            <Route path='stafflogin' element={<StaffLogin/>}/>
            <Route path='attendance' element={<MembersList/>}/>
            <Route path='attendance/register' element={<RegisterMembers/>}/>
            <Route path='signature' element={<Signature/>}/>
            <Route path='support' element={<Support/>}/>
            <Route path='*' element={<Home/>}/>
        </Routes>
      
      </div>

      <Footer/>
    </React.Fragment>
    )
}