import { Box, Card, CardContent, Paper, IconButton, InputBase, Container, Typography, CardActionArea, CardActions, Button } from "@mui/material";
import { useSelector } from "react-redux";
import CheckIcon from '@mui/icons-material/Check';
import { AccountCircle } from '@mui/icons-material';
import { Link } from 'react-router-dom';

const MembersList = ({}) => {
    const allMembers = useSelector((state) => state.attendance.allMembers);
    return (
        <Container component="main" maxWidth="sm">
        <Card sx={{ my: 4, }}>
        <CardContent>

        <Typography gutterBottom variant="h5" component="div" sx={{my: 2}} align="center">Attendance</Typography>
        <Box
        sx={{
            mb: 2,
            display: "flex",
            flexDirection: "column",
            height: 400,
            overflow: "hidden",
            overflowY: "scroll",
            // justifyContent="flex-end" # DO NOT USE THIS WITH 'scroll'
            }}
        >            
        {allMembers.map((member) => (
            <Paper
            component="form"
            sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', width: "100%", my: 2 }}
            >
            <AccountCircle/>
            <InputBase
                sx={{ ml: 1, flex: 1 }}
                placeholder={`${member.username} signs`}
                inputProps={{ 'aria-label': 'add signature' }}
            />
            <IconButton type="button" sx={{ p: '10px' }} aria-label="add">
                <CheckIcon />
            </IconButton>
            </Paper>
        ))}
        </Box>
        </CardContent>
        <CardActions>
            <Button size="small" color="primary" sx={{width: "100%"}}>
            <Link to="register">Register</Link>
            </Button>
        </CardActions>
        </Card>
        </Container>
    )
}

export default MembersList;